import {useState, useEffect} from 'react' 
import styled from 'styled-components'
import arrowTop from '../img/arrow_top.png';

const ButtonWrap = styled.div`
  position:fixed; right:10px; bottom:56px; 
`;
const ScrollTop = styled.button`

transition:all 0.4s;
position:fixed; bottom: 56px; right:10px; 
background: #FFFFFF;
box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.2);
border-radius: 12px;
width:40px; height:40px; border:none; outline:none;
padding:12px 14px 13px;
opacity:0;
&.active{opacity:1;}
img{max-width:100%;}
`;
export const ScrollTopButton = () => {
  // const [ScrollY, setScrollY] = useState(0); // 
  const [BtnStatus, setBtnStatus] = useState(false); //  
  let scrollY = 0;
  //    
  const handleFollow = () => {
    scrollY = window.pageYOffset;
    if(scrollY > 100) {
      // 100   
      setBtnStatus(true);
    } else {
      // 100   
      setBtnStatus(false);
    }
  }


  const handleTop = () => {  //     
    window.scrollTo({
      top: 0,
      behavior: "smooth"
    });
    scrollY = 0;  // ScrollY   
    setBtnStatus(false); // BtnStatus  false  =>  
  }

  useEffect(() => {
    const watch = () => {
      window.addEventListener('scroll', handleFollow)
    }
    watch();
    return () => {
      window.removeEventListener('scroll', handleFollow)
    }
  })


  return <ButtonWrap>
  <ScrollTop onClick={handleTop} className={BtnStatus ? "topBtn active" : "topBtn"}><img src={arrowTop} alt=""/></ScrollTop>
  
  </ButtonWrap>

}